.container {
  background-color: #161616;
  color: #ffffffcc;

  /* position: fixed;
  right: 0;
  top: 0;
  left: 0px; 
  height: 68px; */

  display: flex;
  padding-left: 16px;
  padding-right: 16px;
  margin-right: 0;

  height: 60px;

  align-items: center;

  @media screen and (min-width: 1440px) {
    position: fixed;
    /* right: 0; */
    top: 0;
    /* width: calc(100% - 260px); */
    width: 100%;

    /* left: 260px; */
    height: 68px;
  }
}

.menuBtn {
  padding: 0;
  background-color: transparent;
  border: transparent;
}

.menuIcon {
  stroke: #ffffff;
}

.options {
  display: flex;
  align-items: baseline;
  gap: 14px;
  margin-left: auto;
