.themeBtn {
  padding: 0;
  background-color: transparent;
  border: transparent;
}

.themeIcon {
  stroke: #ffffff;
}

.themeContainer {
  display: flex;
  gap: 4px;
  align-items: flex-end;
}
.themeTitle {
  color: rgba(255, 255, 255, 0.8);
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  letter-spacing: -0.28px;
}
.themeIcon {
  stroke: #ffffffcc;
}

.userInfo {
  display: flex;
  align-items: center;
  gap: 8px;
}
.userName {
  color: #fff;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  letter-spacing: -0.28px;

  color: #ffffff;
}

.userBtn {
  background-color: transparent;
  border: none;
}

